import request from "../../utils/request.js"

export default {
	name: "taber",
	data() {
		return {
			shade:false,
			selection: 1,
			user:uni.getStorageSync('user'),
			avatarUrl:null,
			nickName:null
		};
	},
	props: ["index"],
	created() {
		this.selection = this.index;
	},
	methods: {
					redirectTo(e) {
						uni.navigateTo({
							url: e
						});
					},
					//��¼
					login() {
						let _this = this;
						_this.shade=false;
						// uni.showLoading({
						//     title: '��¼��...'
						// });
						// 1.wx��ȡ��¼�û�code
						uni.login({
							provider: 'weixin',
							success: function(loginRes) {
								let code = loginRes.code;
								if (!_this.isCanUse) {
								
								}
								//2.���û���¼code���ݵ���̨�û��û�SessionKey��OpenId����Ϣ
								request({
									url: '/wechat/login',
									data: {
										code: code,
									},
									method: 'POST',
									header: {
										'content-type': 'application/json'
									},
									success: (res) => {
										//openId����SessionKdy�洢//����loading
										console.log(res)
										if (res.data.status === 200) {
											uni.setStorageSync('token', res.data.token);
										}
										console.log('token', uni.getStorageSync('token'))
										//��Ȩ�ɹ�����ȡ�û����ݴ������ݿ�
										_this.shade = true
										// uni.hideLoading();
									}
								});
							},
						});
					},
					//����̨������Ϣ
					updateUserInfo() {
						let _this = this;
						console.log('_this.nickName', _this.nickName)
						request({
							url: '/wechat/update/userinfo', //�������˵�ַ
							data: {
								phoneNumber: null,
								nickName: _this.nickName,
								headUrl: _this.avatarUrl,
								gender: _this.gender
							},
							method: 'POST',
							header: {
								'content-type': 'application/json'
							},
							success: (res) => {
								console.log(res)
								if (res.data.status === 200) {
									uni.setStorageSync('user',res.data.data)
									uni.navigateTo({
										url:'../../pages/my/my'
									})
								}
							}
						});
					},
					async getUserInfo(){
						let _this = this
						//�ǵ�һ����Ȩ��ȡ�û���Ϣ
						await uni.getUserInfo({
							provider: 'weixin',
							success: function(infoRes) {
								//��ȡ�û���Ϣ����������Ϣ���·���
								_this.nickName = infoRes.userInfo.nickName; //�ǳ�
								_this.avatarUrl = infoRes.userInfo.avatarUrl; //ͷ��
								_this.updateUserInfo(); //���ø�����Ϣ����
							}
						});
					}
	}
};
